<template>
    <section class="min-h-full w-full pb-[5rem] lg:pb-[2rem]">
        <div class="mb-8 flex justify-between items-center">
            <h3 class="title-vista text-2xl font-semibold text-neutral-600">Administrar cuentas</h3>
            <button @click="openModal"
                class="py-2 px-3 rounded-xl bg-orange-400 text-white text-sm hover:bg-orange-500 transition duration-300">
                Crear cuenta
            </button>
        </div>
        <div class="text-sm overflow-x-auto shadow-none lg:shadow-md sm:rounded-2xl">
            <TableUsers v-if="!isMobile" :listaUsuarios="ListUsers" @eliminar-usuario="eliminarUsuario"
                @editar-usuario="editarUsuario" />
            <ItemsTableMobil v-else :listaUsuarios="ListUsers" @eliminar-usuario="eliminarUsuario"
                @editar-usuario="editarUsuario" />
        </div>
        <!-- Modal -->
        <transition name="modal" mode="out-in">
            <ModalCreate v-show="openModalV" @close-modal="closeModal" @save-datos="crearCuenta" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalDelete v-if="confirmDelete" @aceptar-eliminar="aceptarEliminar" @cancelar-eliminar="cancelarEliminar"
                pregunta="¿Estás seguro de eliminar esta cuenta?" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalEditar v-if="openModalEdit" @aceptar-editar="aceptarEditar" @cancelar-editar="cancelarEditar"
                :idUsuario="idUsuarioSet" :idEmpresa="idEmpresaSet" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModaError v-if="openModalError" title="Advertencia" mensaje="Se debe rellenar todos los campos"
                @close-modal="closeModalError" />
        </transition>
        <transition name="modal" mode="out-in">
            <ConfirmEdit v-if="openSuccessEdit" title="Logrado" mensaje="Se ha editado correctamente"
                @close-modal="closeSuccessEdit" />
        </transition>
    </section>
</template>
  
<script>
import { ref, onMounted } from 'vue';
import store from '@/store';
import { listarUsuarios, crearUsuarios, guardarImagenLogo, eliminarUsuarioCuenta, editarUsuarios } from '@/services/AdminUsers';
import TableUsers from '@/components/cuentas/TableUsers.vue';
import ModalCreate from '@/components/cuentas/ModalCreate.vue';
import ModaError from '../components/generales/ModalError.vue';
import ConfirmEdit from '../components/generales/ConfirmEdit.vue';
import ModalDelete from '@/components/generales/ModalDelete.vue';
import ModalEditar from '../components/cuentas/ModalEditar.vue';
import ItemsTableMobil from '@/components/cuentas/ItemsTableMobil.vue';

export default {
    name: 'AdminCuentas',
    components: {
        TableUsers, ModalCreate,
        ModalDelete, ModaError,
        ModalEditar, ConfirmEdit,
        ItemsTableMobil
    },
    setup() {
        const ListUsers = ref([]);
        const openModalV = ref(false);
        const confirmDelete = ref(false);
        const openModalError = ref(false);
        const openModalEdit = ref(false);
        const idUsuarioSet = ref("");
        const idEmpresaSet = ref("");
        const openSuccessEdit = ref(false);
        const isMobile = ref(false);
        onMounted(() => {
            listarUsuarios(store.state.codempresa, store.state.tipousuario)
                .then(response => {
                    ListUsers.value = response.data.usuarios;
                });
            const handleResize = () => {
                isMobile.value = window.innerWidth <= 768; // Define aquí el ancho máximo para considerar como pantalla pequeña
            };
            // Llama a la función handleResize al cargar la página y en cada cambio de tamaño de la ventana
            handleResize();
            window.addEventListener("resize", handleResize);
        });
        const openModal = () => {
            openModalV.value = true;
        };
        const closeModal = () => {
            openModalV.value = false;
        };
        const closeModalError = () => {
            openModalError.value = false;
        };
        const closeSuccessEdit = () => {
            openSuccessEdit.value = false;
            window.location.reload();
        }
        const crearCuenta = (dataformulario) => {
            const usuario = dataformulario.usuario;
            const empresa = dataformulario.empresa;
            const password = dataformulario.password;
            const ubigeo = dataformulario.ubigeo;
            const direccion = dataformulario.direccion;
            const correo = dataformulario.correo;
            const ruc = dataformulario.ruc;
            const telefono = dataformulario.telefono;
            const domicilio_fiscal = dataformulario.domicilio_fiscal;
            const logoImage = dataformulario.logoImage;
            if (usuario == '' || empresa == '' || password == '' || ubigeo == '' ||
                direccion == '' || correo == '' || telefono == '' || domicilio_fiscal == '' || ruc == '') {
                openModalV.value = false;
                openModalError.value = true;
            } else {
                crearUsuarios(store.state.codempresa, store.state.tipousuario, usuario, empresa, password, ubigeo, direccion, correo, telefono, domicilio_fiscal, ruc)
                    .then(response => {
                        guardarImagenLogo(logoImage, response.data.codempresa)
                        openModalV.value = false;
                        window.location.reload();
                    });
            }
        };
        const eliminarUsuario = (idUsuario, idEmpresa) => {
            idUsuarioSet.value = idUsuario;
            idEmpresaSet.value = idEmpresa;
            confirmDelete.value = true;
        }
        const aceptarEliminar = () => {
            eliminarUsuarioCuenta(idUsuarioSet.value, idEmpresaSet.value, store.state.tipousuario)
                .then(response => {
                    console.log(response);
                    window.location.reload();
                    confirmDelete.value = false;
                    idUsuarioSet.value = ""
                    idEmpresaSet.value = ""
                });
        }
        const cancelarEliminar = () => {
            confirmDelete.value = false;
            idUsuarioSet.value = ""
            idEmpresaSet.value = ""
        }
        const editarUsuario = (idUsuario, idEmpresa) => {
            idUsuarioSet.value = idUsuario;
            idEmpresaSet.value = idEmpresa;
            openModalEdit.value = true;
        }
        const cancelarEditar = () => {
            openModalEdit.value = false;
        }

        const aceptarEditar = (dataformulario) => {
            const usuario = dataformulario.usuario;
            const empresa = dataformulario.empresa;
            const password = dataformulario.password;
            const ubigeo = dataformulario.ubigeo;
            const direccion = dataformulario.direccion;
            const correo = dataformulario.correo;
            const ruc = dataformulario.ruc;
            const telefono = dataformulario.telefono;
            const domicilio_fiscal = dataformulario.domicilio_fiscal;
            if (usuario == '' || empresa == '' || password == '' || ubigeo == '' ||
                direccion == '' || correo == '' || telefono == '' || domicilio_fiscal == '' || ruc == '') {
                openModalEdit.value = false;
                openModalError.value = true;
            } else {
                editarUsuarios(idUsuarioSet.value, idEmpresaSet.value, usuario, empresa, password, ubigeo, direccion, correo, telefono, domicilio_fiscal, ruc)
                    .then(response => {
                        console.log(response)
                        openSuccessEdit.value = true;
                        openModalEdit.value = false;
                    });
            }
        }
        return {
            ListUsers, openModalV,
            openModal, closeModal,
            crearCuenta, confirmDelete,
            eliminarUsuario, aceptarEliminar,
            cancelarEliminar, closeModalError,
            cancelarEditar, openModalError,
            openModalEdit, editarUsuario,
            idUsuarioSet, idEmpresaSet,
            aceptarEditar, openSuccessEdit,
            closeSuccessEdit, isMobile
        };
    },
};
</script>
<style scoped>
.modal-enter-active,
.modal-leave-active {
    transition: opacity 0.3s;
}

.modal-enter-from,
.modal-leave-to {
    opacity: 0;
}
</style>


