<template>
    <div class="h-full flex-col gap-3 bg-inherit">
        <div class="w-full px-4 py-5 rounded-xl bg-white overflow-hidden my-2 shadow-md"
        v-for="usuario in listaUsuarios" :key="usuario.id">
            <p class="py-1"><span class="font-bold pr-2">Usuario: </span><span>{{ usuario.usuario }}</span></p>
            <p class="py-1"><span class="font-bold pr-2">Empresa:</span><span>{{ usuario.nombre }}</span></p>
            <p class="py-1"><span class="font-bold pr-2">Codigo empresa: </span><span>{{ usuario.codempresa }}</span></p>
            <p class="py-1"><span class="font-bold pr-2">Dirección: </span><span>{{ usuario.direccion }}</span></p>
            <p class="py-1"><span class="font-bold pr-2">Correo:</span> <span>{{ usuario.correo_electronico }}</span></p>
            <p class="py-1"><span class="font-bold pr-2">Telefono:</span> <span>{{ usuario.telefono }}</span></p>
            <div class="py-3"><span class="pr-4 font-bold">Acciones:</span>
                <a href="#" class="mr-2 p-2 font-medium text-white bg-green-500 
                hover:bg-green-700 ease-in duration-300 rounded-lg"
                @click.prevent="editarUsuario(usuario.id_usuario, usuario.id_empresa)">
                    Editar
                </a>
                <a href="#" class="p-2 font-medium text-white  bg-red-500 hover:bg-red-700 ease-in duration-300 rounded-lg"
                @click.prevent="eliminarUsuario(usuario.id_usuario, usuario.id_empresa)">Eliminar</a>
            </div>
        </div>

    </div>
</template>
<script>
import { defineComponent } from "vue";

export default defineComponent({
    name: "ItemsTableMobil",
    props: {
        listaUsuarios: Array
    },
    methods: {
        eliminarUsuario(idUsuario, idEmpresa){
            this.$emit('eliminar-usuario', idUsuario, idEmpresa)
        },
        editarUsuario(idUsuario, idEmpresa){
            this.$emit('editar-usuario', idUsuario, idEmpresa)
        }
    }
})
</script>