<template>
  <div class="fixed inset-0 flex items-center justify-center z-[9999] py-5">
    <div class="absolute inset-0 bg-black opacity-30 pointer-events-none"></div>
    <!-- Container modal -->
    <div class="relative z-10 p-4 bg-white rounded-xl lg:min-w-[350px] lg:max-w-[450px] min-w-[340px] max-w-[350px]">
      <form class="p-2 text-gray-700 w-full h-full text-sm">
        <h3 class="text-center font-bold text-lg">Crear cuenta</h3>
        <ItemForm @value-input="updateusuario" label="Usuario" name="usuario" id="usuario"
          placeholder="Ingrese un usuario" value="" />
        <span v-if="msgValidacionUsuario != ''">
          <span v-if="msgValidacionUsuario == 'true'" class="text-green-400">Este usuario está disponible</span>
          <span v-else-if="msgValidacionUsuario == 'false2'" class="text-orange-400">El usuario debe tener más de
            3 caracteres</span>
          <span v-else class="text-red-400">Este usuario ya existe</span>
        </span>
        <ItemForm @value-input="updatebusinnes" label="Razón social" name="empresa" id="empresa"
          placeholder="Ingrese el nombre de la empresa" value="" />
        <ItemForm @value-input="saveRuc" label="RUC" name="ruc" placeholder="Ruc del cliente" value="" />
        <LogoimgField @file-selected="updateImg" />
        <PasswordField @update-paswoord="updatepassword" texto="Contraseña" placeholder="Ingrese contraseña" />
        <PasswordField @update-paswoord="updatepasswordRepeat" texto="Confirmar contraseña"
          placeholder="Repita su contraseña" />
        <p :class="{ 'text-green-500 text-xs': msgValidacion === 'Las contraseñas coinciden', 'text-red-400 text-xs': msgValidacion === 'Las contraseñas no coinciden' }"
          v-if="msgValidacion !== ''">{{ msgValidacion }}</p>
        <!-- Dropdown para la sección Ubicación -->
        <div class="dropdown">
          <button
            class="dropdown-toggle bg-orange-400 flex justify-between items-center w-full rounded-lg text-white px-5 py-2 mt-2"
            @click.prevent="toggleUbicacion" :class="{ 'bg-orange-500': mostrarUbicacion }">
            <span>Datos ubicación</span>
            <i class="fa-solid fa-angle-down"></i>
          </button>
          <div class="dropdown-menu mx-2 px-2 py-4 rounded-md bg-slate-200 transition-max-height duration-300" :class="{
            'max-h-[500px]': mostrarUbicacion,
            'max-h-0': !mostrarUbicacion,
            'overflow-hidden': !mostrarUbicacion,
            'pt-0 pb-0': !mostrarUbicacion
          }">
            <ItemForm @value-input="saveDireccion" label="Dirección" name="direccion"
              placeholder="Dirección del cliente" />
            <ItemForm @value-input="saveCodigoDomicilioFiscal" label="Codigo de domicilio fiscal" name="codigodomicilio"
              placeholder="Codigo de domicilio fiscal de la empresa" />
            <ItemForm @value-input="saveUbigeo" label="Ubigeo" name="ubigeo" placeholder="Ubigeo del cliente" />
          </div>
        </div>
        <!-- Dropdown para la sección Contacto -->
        <div class="dropdown">
          <button
            class="dropdown-toggle bg-orange-400 flex justify-between items-center w-full rounded-lg text-white px-5 py-2 mt-2"
            @click.prevent="toggleContacto" :class="{ 'bg-orange-500': mostrarContacto }">
            <span>Datos contacto</span>
            <i class="fa-solid fa-angle-down"></i>
          </button>
          <div class="dropdown-menu mx-2 px-2 py-4 rounded-md bg-slate-200 transition-max-height duration-300" :class="{
            'max-h-[500px]': mostrarContacto,
            'max-h-0': !mostrarContacto,
            'overflow-hidden': !mostrarContacto,
            'pt-0 pb-0': !mostrarContacto
          }">
            <ItemForm @value-input="saveCorreo" label="Correo" name="correo" placeholder="Correo del cliente" />
            <ItemForm @value-input="saveTelefono" label="Teléfono" name="telefono" placeholder="Teléfono del cliente" />
          </div>
        </div>
      </form>
      <div class="btns flex gap-2 justify-center items-center pt-2">
        <button @click="closeModal"
          class="bg-gray-400 hover:bg-gray-500 text-white text-sm p-2 px-4 rounded-xl transition duration-300">Cerrar</button>
        <button @click="saveDatos"
          class="bg-blue-400 hover:bg-blue-500 text-white text-sm p-2 px-4 rounded-xl transition duration-300">Crear</button>
      </div>
    </div>
    <!-- Container modal -->
  </div>
</template>
  
<script>
import { ref } from 'vue';
import PasswordField from '@/components/generales/PasswordField.vue';
import ItemForm from '../generales/ItemForm.vue';
import LogoimgField from '@/components/cuentas/LogoimgField.vue';
import { validarUsuariosUnicos } from '@/services/AdminUsers';

export default {
  name: 'ModalCreate',
  components: { PasswordField, LogoimgField, ItemForm },
  emits: ['close-modal', 'save-datos'],
  setup(_, { emit }) {
    const logoImage = ref(null);
    const usuario = ref('');
    const empresa = ref('');
    const password = ref('');
    const direccion = ref('');
    const domiciliofiscal = ref('');
    const ubigeo = ref('');
    const correo = ref('');
    const telefono = ref('');
    const ruc = ref('');
    const repeatpassword = ref('');
    const msgValidacion = ref('');
    const nombreArchivo = ref('');
    const mostrarUbicacion = ref(false);
    const mostrarContacto = ref(false);
    const msgValidacionUsuario = ref('');

    const toggleUbicacion = () => {
      mostrarUbicacion.value = !mostrarUbicacion.value;
    };

    const toggleContacto = () => {
      mostrarContacto.value = !mostrarContacto.value;
    };

    const updateusuario = (value) => {
      usuario.value = value;
      if (value.length > 3) {
        validarUsuariosUnicos(value)
          .then((response) => {
            if (response.data.status) {
              msgValidacionUsuario.value = "true"
            } else {
              msgValidacionUsuario.value = "false"
            }
          })
          .catch(() => {
            msgValidacionUsuario.value = "false"
          })
      } else {
        msgValidacionUsuario.value = "false2";
      }
    };

    const updatebusinnes = (value) => {
      empresa.value = value;
    };

    const updatepassword = (value) => {
      password.value = value;
    };

    const updatepasswordRepeat = (value) => {
      repeatpassword.value = value;
      validatePassword();
    };

    const updateImg = (file) => {
      logoImage.value = file;
    };

    const saveDireccion = (value) => {
      direccion.value = value;
    };

    const saveCodigoDomicilioFiscal = (value) => {
      domiciliofiscal.value = value;
    };

    const saveUbigeo = (value) => {
      ubigeo.value = value;
    };

    const saveRuc = (value) => {
      ruc.value = value;
    };

    const saveCorreo = (value) => {
      correo.value = value;
    };

    const saveTelefono = (value) => {
      telefono.value = value;
    };

    const closeModal = () => {
      emit('close-modal');
    };

    const saveDatos = () => {
      emit('save-datos', {
        /* datos del formulario */
        usuario: usuario.value,
        empresa: empresa.value,
        password: password.value,
        logoImage: logoImage.value,
        ubigeo: ubigeo.value,
        ruc: ruc.value,
        direccion: direccion.value,
        correo: correo.value,
        telefono: telefono.value,
        domicilio_fiscal: domiciliofiscal.value,
      });
    };

    const validatePassword = () => {
      if (password.value === repeatpassword.value) {
        msgValidacion.value = 'Las contraseñas coinciden';
      } else {
        msgValidacion.value = 'Las contraseñas no coinciden';
      }
    };

    return {
      logoImage,
      usuario,
      empresa,
      password,
      direccion,
      domiciliofiscal,
      ubigeo,
      correo,
      msgValidacionUsuario,
      telefono,
      ruc,
      repeatpassword,
      msgValidacion,
      nombreArchivo,
      mostrarUbicacion,
      mostrarContacto,
      toggleUbicacion,
      toggleContacto,
      updateusuario,
      updatebusinnes,
      updatepassword,
      updatepasswordRepeat,
      updateImg,
      saveDireccion,
      saveCodigoDomicilioFiscal,
      saveUbigeo,
      saveRuc,
      saveCorreo,
      saveTelefono,
      closeModal,
      saveDatos,
      validatePassword,
    };
  },
};
</script>

