<template>
    <table class="w-full text-xs text-left text-gray-500 dark:text-gray-400">
        <thead class="text-xs uppercase bg-gray-400 text-white">
            <tr>
                <th scope="col" class="px-6 py-3">
                    Usuario
                </th>
                <th scope="col" class="px-6 py-3">
                    Empresa
                </th>
                <th scope="col" class="px-6 py-3">
                    Codigo empresa
                </th>
                <th scope="col" class="px-6 py-3">
                    Dirección
                </th>
                <th scope="col" class="px-6 py-3">
                    Correo
                </th>
                <th scope="col" class="px-6 py-3">
                    Telefono
                </th>
                <th scope="col" class="px-6 py-3">
                    Acciones
                </th>
            </tr>
        </thead>
        <tbody>
            <tr v-if="listaUsuarios.length === 0">
                <td class="border px-4 py-4 text-xs text-center" colspan="8">No se encontró ningún dato
                    para esta tabla</td>
            </tr>
            <tr class="bg-white border-b hover:bg-gray-100 ease-in duration-300"
                v-for="usuario in listaUsuarios" :key="usuario.id">
                <th scope="row" class="flex items-center px-6 py-3 text-gray-900 whitespace-nowrap ">
                    <img class="w-10 h-10 rounded-full" src="../../assets/icon-user.png" alt="Icon user">
                    <div class="pl-3">
                        <div class="text-gray-800 font-medium">{{ usuario.usuario }}</div>
                    </div>
                </th>
                <td class="px-6 py-3 text-gray-800">
                    {{ usuario.nombre }}
                </td>
                <td class="px-6 py-3 text-gray-800">
                    {{ usuario.codempresa }}
                </td>
                <td class="px-6 py-3 text-gray-800">
                    {{ usuario.direccion }}
                </td>
                <td class="px-6 py-3 text-gray-800">
                    {{ usuario.correo_electronico }}
                </td>
                <td class="px-6 py-3 text-gray-800">
                    {{ usuario.telefono }}
                </td>
                <td class="px-6 py-3">
                    <a href="#"
                        class="mr-2 p-2 font-medium text-white  bg-green-500 hover:bg-green-700 ease-in duration-300 rounded-lg" @click.prevent="editarUsuario(usuario.id_usuario, usuario.id_empresa)">Editar</a>
                    <a href="#"
                        class="p-2 font-medium text-white  bg-red-500 hover:bg-red-700 ease-in duration-300 rounded-lg" @click.prevent="eliminarUsuario(usuario.id_usuario, usuario.id_empresa)">Eliminar</a>
                </td>
            </tr>
        </tbody>
    </table>
</template>
<script>
export default ({
    name: "TableUsers",
    props: {
        listaUsuarios: Array
    },
    methods: {
        eliminarUsuario(idUsuario, idEmpresa){
            this.$emit('eliminar-usuario', idUsuario, idEmpresa)
        },
        editarUsuario(idUsuario, idEmpresa){
            this.$emit('editar-usuario', idUsuario, idEmpresa)
        }
    }
})
</script>