<template>
    <div class="fixed inset-0 flex items-center justify-center z-[9999]">
        <div class="absolute inset-0 bg-black opacity-30 pointer-events-none"></div>
        <!-- Container modal -->
        <div class="relative z-10 p-4 bg-white rounded-xl lg:min-w-[350px] lg:max-w-[450px] min-w-[340px] max-w-[350px]">
            <form class="p-2 text-gray-700 w-full h-full text-sm">
                <h3 class="text-center font-bold text-lg">Editar cuenta</h3>
                <ItemForm @value-input="updateusuario" label="Usuario" name="usuario" id="usuario"
                    placeholder="Ingrese un usuario" :value="usuario" />
                <ItemForm @value-input="updatebusinnes" label="Razón social" name="empresa" id="empresa"
                    placeholder="Ingrese el nombre de la empresa" :value="empresa" />
                <ItemForm @value-input="saveRuc" label="RUC" name="ruc" placeholder="Ruc del cliente" :value="ruc" />
                <PasswordField @update-paswoord="updatepassword" texto="Contraseña" placeholder="Ingrese contraseña"
                    :valor="password" />
                <!-- Dropdown para la sección Ubicación -->
                <div class="dropdown">
                    <button
                        class="dropdown-toggle bg-orange-400 flex justify-between items-center w-full rounded-lg text-white px-5 py-2 mt-2"
                        @click.prevent="toggleUbicacion" :class="{ 'bg-orange-500': mostrarUbicacion }">
                        <span>Datos ubicación</span>
                        <i class="fa-solid fa-angle-down"></i>
                    </button>
                    <div class="dropdown-menu mx-2 px-2 py-4 rounded-md bg-slate-200 transition-max-height duration-300"
                        :class="{
                            'max-h-[500px]': mostrarUbicacion,
                            'max-h-0': !mostrarUbicacion,
                            'overflow-hidden': !mostrarUbicacion,
                            'pt-0 pb-0': !mostrarUbicacion
                        }">
                        <ItemForm @value-input="saveDireccion" label="Dirección" name="direccion"
                            placeholder="Dirección del cliente" :value="direccion" />
                        <ItemForm @value-input="saveCodigoDomicilioFiscal" label="Codigo de domicilio fiscal"
                            name="codigodomicilio" placeholder="Codigo de domicilio fiscal de la empresa"
                            :value="domiciliofiscal" />
                        <ItemForm @value-input="saveUbigeo" label="Ubigeo" name="ubigeo" placeholder="Ubigeo del cliente"
                            :value="ubigeo" />
                    </div>
                </div>
                <!-- Dropdown para la sección Contacto -->
                <div class="dropdown">
                    <button
                        class="dropdown-toggle bg-orange-400 flex justify-between items-center w-full rounded-lg text-white px-5 py-2 mt-2"
                        @click.prevent="toggleContacto" :class="{ 'bg-orange-500': mostrarContacto }">
                        <span>Datos contacto</span>
                        <i class="fa-solid fa-angle-down"></i>
                    </button>
                    <div class="dropdown-menu mx-2 px-2 py-4 rounded-md bg-slate-200 transition-max-height duration-300"
                        :class="{
                            'max-h-[500px]': mostrarContacto,
                            'max-h-0': !mostrarContacto,
                            'overflow-hidden': !mostrarContacto,
                            'pt-0 pb-0': !mostrarContacto
                        }">
                        <ItemForm @value-input="saveCorreo" label="Correo" name="correo" placeholder="Correo del cliente"
                            :value="correo" />
                        <ItemForm @value-input="saveTelefono" label="Teléfono" name="telefono"
                            placeholder="Teléfono del cliente" :value="telefono" />
                    </div>
                </div>
            </form>
            <div class="btns flex gap-2 justify-center items-center pt-2">
                <button @click="closeModal"
                    class="bg-gray-400 hover:bg-gray-500 text-white text-sm p-2 px-4 rounded-xl transition duration-300">Cancelar</button>
                <button @click="saveDatos"
                    class="bg-blue-400 hover:bg-blue-500 text-white text-sm p-2 px-4 rounded-xl transition duration-300">Aceptar</button>
            </div>
        </div>
        <!-- Container modal -->
    </div>
</template>
  
<script>
import { ref, onMounted } from 'vue';
import PasswordField from '@/components/generales/PasswordField.vue';
import ItemForm from '../generales/ItemForm.vue';
import { buscarUsuarioCuenta } from '@/services/AdminUsers';
import md5 from 'md5';

export default {
    name: 'ModalEditar',
    components: { PasswordField, ItemForm },
    emits: ['cancelar-editar', 'aceptar-editar'],
    props: {
        idUsuario: String,
        idEmpresa: String
    },
    setup(props, { emit }) {
        const usuario = ref('');
        const empresa = ref('');
        const password = ref('');
        const passwordNuevo = ref('');
        const direccion = ref('');
        const domiciliofiscal = ref('');
        const ubigeo = ref('');
        const correo = ref('');
        const telefono = ref('');
        const ruc = ref('');
        const nombreArchivo = ref('');
        const mostrarUbicacion = ref(false);
        const mostrarContacto = ref(false);


        const toggleUbicacion = () => {
            mostrarUbicacion.value = !mostrarUbicacion.value;
        };

        const toggleContacto = () => {
            mostrarContacto.value = !mostrarContacto.value;
        };

        const updateusuario = (value) => {
            usuario.value = value;
        };

        const updatebusinnes = (value) => {
            empresa.value = value;
        };

        const updatepassword = (value) => {
            passwordNuevo.value = value;
        };

        const saveDireccion = (value) => {
            direccion.value = value;
        };

        const saveCodigoDomicilioFiscal = (value) => {
            domiciliofiscal.value = value;
        };

        const saveUbigeo = (value) => {
            ubigeo.value = value;
        };

        const saveRuc = (value) => {
            ruc.value = value;
        };

        const saveCorreo = (value) => {
            correo.value = value;
        };

        const saveTelefono = (value) => {
            telefono.value = value;
        };

        const closeModal = () => {
            emit('cancelar-editar');
        };

        const saveDatos = () => {
            let contraseñaFinal = password.value;
            if (passwordNuevo.value !== "") {
                contraseñaFinal = md5(passwordNuevo.value);
            }
            emit('aceptar-editar', {
                /* datos del formulario */
                usuario: usuario.value,
                empresa: empresa.value,
                password: contraseñaFinal,
                ubigeo: ubigeo.value,
                ruc: ruc.value,
                direccion: direccion.value,
                correo: correo.value,
                telefono: telefono.value,
                domicilio_fiscal: domiciliofiscal.value,
            });
        };
        onMounted(() => {
            buscarUsuarioCuenta(props.idUsuario, props.idEmpresa)
                .then(response => {
                    usuario.value = response.data.usuario[0].usuario;
                    empresa.value = response.data.usuario[0].nombre;
                    password.value = response.data.usuario[0].clave;
                    ubigeo.value = response.data.usuario[0].ubigeo;
                    direccion.value = response.data.usuario[0].direccion;
                    correo.value = response.data.usuario[0].correo_electronico;
                    telefono.value = response.data.usuario[0].telefono;
                    domiciliofiscal.value = response.data.usuario[0].codigo_del_domicilio_fiscal;
                    ruc.value = response.data.usuario[0].numero_documento;
                })
        })

        return {
            usuario,
            empresa,
            password,
            direccion,
            domiciliofiscal,
            ubigeo,
            correo,
            telefono,
            ruc,
            nombreArchivo,
            mostrarUbicacion,
            mostrarContacto,
            toggleUbicacion,
            toggleContacto,
            updateusuario,
            updatebusinnes,
            updatepassword,
            saveDireccion,
            saveCodigoDomicilioFiscal,
            saveUbigeo,
            saveRuc,
            saveCorreo,
            saveTelefono,
            closeModal,
            saveDatos
        };
    },
};
</script>
